body {
  margin: 0;
}

:root {
  --maincolor: #000;
  --altcolor: #00acdc;
}

.header {
  justify-content: space-between;
  align-items: center;
  height: 60px;
  padding: 0 50px;
  display: flex;
}

.nav {
  text-align: center;
  justify-content: center;
  align-items: center;
  height: 60px;
  padding: 0;
  display: flex;
}

.header__link {
  width: 40px;
  height: 40px;
}

.nav_list {
  text-transform: uppercase;
  text-align: center;
  justify-content: center;
  align-items: center;
  height: 60px;
  margin: 0;
  padding: 0;
  display: flex;
}

.nav_item {
  align-content: center;
  height: 60px;
  margin-right: 20px;
  font-size: 12px;
  list-style-type: none;
}

.nav_link {
  color: var(--maincolor);
  text-wrap: nowrap;
  height: 60px;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  line-height: 60px;
  text-decoration: none;
  display: block;
}

.nav_link:hover {
  color: var(--altcolor);
}

.is-active {
  color: var(--altcolor);
  position: relative;
}

.is-active:after {
  content: " ";
  background-color: var(--altcolor);
  border-radius: 4px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  left: 0;
  right: 0;
  transform: translateY(-100%);
}

.nav_item:last-child {
  margin-right: 0;
}
